    --sidebarColorLight: #fffbeb;
    --addButtonText: #a81f00;
    --highlightColor: #ffedd5;
    --profileImg: '../assets/user6.png';
}
* {
    font-family: "Reddit Mono", monospace;
    font-optical-sizing: auto;
    font-style: normal;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
* button {
    cursor: pointer;
}

body {
    display: flex;
    height: 100vh;
}
/* Sidebar Stuff */
.sidebar {
    padding: .5rem;
    height: auto;
    width: 210px;
    max-width: 420px;
    min-width: 210px;
    flex-shrink: 0;
    background-color: var(--sidebarColorLight);
    box-shadow: 8px -2px 23px -6px rgba(145,135,103,0.75);
-webkit-box-shadow: 8px -2px 23px -6px rgba(145,135,103,0.75);
-moz-box-shadow: 8px -2px 23px -6px rgba(145,135,103,0.75);
}

.sidebar .name{
    display: flex;
    width: auto;
    align-items: center;
    justify-content: start;
}

.sidebar .name-name {
    font-size: 1.8rem;
}

.sidebar .name .name-icon {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: contain;
    background-repeat: no-repeat;
    margin: 8px;
}

.sidebar .name-notification-icon {
    padding-left: 50px;
    
}

nav {
padding: .5rem;
display: flex;
flex-direction: column;
}

.nav-line {
    width: 90%;
    height: 1px;
    background-color: grey;
    margin-top: 3px;
    margin-bottom: 12px;
}

nav {
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: flex-start;
    margin-bottom: 1rem;
}

nav button {
    background-color: transparent;
    border-style: none;
    font-size: 1.1rem;
    margin-top: 12px;
    margin-bottom: 12px;
    width: 100%;
}

nav button:hover {
    border-bottom: solid red 1px;
}

nav .active {
    border-radius: 8px;
    background-color: var(--highlightColor);
}

/* Main Content  */
main{
    margin: 1rem;
    background-color: #fff;
}

.main-line {
    width: 100%;
    height: 1px;
    background-color: #e5e5e5;;
    margin-top: 12px;
    margin-bottom: 12px;
}

.tasks-today {
    margin-top: 16px;
    margin-bottom: 16px;
}
.date {
    color: #dc4c3e;
    display: flex;
    justify-content: start;
    align-items: center;
}
.task-1 input, .task-1 label {
    font-size: 1.2rem;
}

.task-1 input {
    width: 20px;
    height: 20px;
}
/* Dialog container Stuff */
.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5); /* Semi-transparent overlay */
    display: flex;
    justify-content: center;
    align-items: center;
  }

.dialog-container {
    background-color: var(--sidebarColorLight);
    display: flex;
    width: 60%;
    padding: 1rem;
    border-radius: 12px;
    justify-content: center;
}
.dialog-container form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    
}
.dialog-container form input, .dialog-container form button {
margin: 8px;
padding: 6px;
}






